import React from 'react'
import TplGlobal2 from './template-global2'
import Social from '../blocks/social'

const TplPost = (props) => {
    const {post = {
        id: 2,
        type: 1,
        title: "Common Types of Breast Cancer",
        text: "Introduction from Mayo Clinic to the common types of breast cancer including ductal and lobular breast cancer, and invasive and non-invasive breast cancer.",
        date: "30 days ago",
        image: undefined,
        videoId: 'FTH56ifHt28',
        podcast: undefined,
        postedBy: users.u14,
        expert: users.u11,
        citizen: users.u17,
        contributors: [users.u21,users.u28],
        levels: [{type:2}],
        comments: [
            {
                user: users.u8,
                text: "Thatâ€™s good to know",
                date: "20 days ago",
            },
            {
                user: users.u32,
                text: "Well, I do not teint my hair",
                date: "20 days ago",
            }
        ]

    }} = props;






    const {title, text, image, videoId, info} = post;
    const {type, categories, selected, className} = post;
    const {nbcomments, nbvotes, nbshared, levels} = post;
    const {postedBy, expert, citizen, contributors, comments} = post;

    const getText = (text) => {
        
        return text.split('##').map((item, key) => {
            return <span key={key}>{item}<br/></span>
          });

    }
    const getTypes = () => {

    }
    const getImg = (image) => {
        if (image) {
            return <img src={`/media/images/${image}`} width={`400px`} />
        }
    }

    const getPicture = (userId) => (<img src={`/media/users/${userId}.png`} />);

    
    const getVideo = (videoId, size) => {
        if (videoId) {

       
         return (   
            <div>
            <iframe type="text/html" width={`${size || '400'}`} height="270"
            src={`https://www.youtube.com/embed/${videoId}?enablejsapi=1`}
            frameBorder="0"
            id={`video${videoId}`}></iframe>

            
            </div>
);
        }
    }




    return (
    

        <TplGlobal2 title={post.title}>
           <div className="main">
                <div className="header">
                    
                    <div className="type"></div> {title}
                </div>


                <div className="center">
                    <div className="img">
                        {getImg(img)}
                        {getVideo(videoId)}

                    </div>

                    
                    <div className="content">
                        <div className="body">
                        {getText(text)}
                        </div>
                        <div className="footer">
                            
                        <Social social={ {
        nbComments: 0,
        nbVotes: {
            up: 0,
            down: 0
        },
        nbShared: 0,
        levels: post.levels,
        }} />
                            
                        </div>
                    </div>
                </div>

                
                <div className="info">
                    <div className="posted"><div className="desc">Posted by:</div> {getPicture(postedBy.id)} <div className="name">{postedBy.name}</div> <div className="date">- {post.date}</div> </div>
                    <div className="expert"><div className="desc">Expert reviews by:</div> {getPicture(expert.id)} <div className="name">{expert.name}</div></div>
                    <div className="citizen"><div className="desc">Citizen reviews by:</div> {getPicture(citizen.id)} <div className="name">{citizen.name}</div></div>
                    <div className="break"></div>
                    <div className="contributors">
                        <div className="desc">Contributors:</div>
                        {
                            contributors.map((contributor, index) => {
                                return (<div className="contributor">{getPicture(contributor.id)} <div className="name">{contributor.name}</div></div>)
                            })
                        }
                    </div>
                        
                        
                </div>

                <div className="comments">
                   
                    <input type="text" className="input" disabled placeholder="Sign in to comment"/>
                    {
                        comments.map((comment, index) => {
                            return (
                   
                        <div className="comment">
                            <div className="user">{getPicture(comment.user.id)} <div className="name">{comment.text}</div> <div className="date">- {comment.date}</div></div>
                            <div className="data">
                                {getText(comment.text)}
                                {getImg(comment.img)}
                                {getVideo(comment.videoId, 600)}

                            </div>
                            <Social social={ {
        nbComments: 0,
        nbVotes: {
            up: 0,
            down: 0
        },
        nbShared: 0,
        levels: [],
        }} />
                        </div>
                            )
                        })
                    }
                </div>



            </div>

                <style jsx>
                {`  
                
                .input {
                    border: 1px solid #D3DDDD;
                    border-radius: 5px 0px 0px 0px;
                    width: 660px;
                    height: 40px;
                    margin-bottom: 20px;
                }

                .input:disabled {
                    background-color: #fff;
                }



                ::placeholder {
                    color: #D3DDDD;
                    font: 12px/19px Arial;
                    letter-spacing: 0;
                    color: #CCCCCC;
                    padding-left: 10px;
                }

                .comments {
                    /*height: 400px;*/
                    margin-top: 20px;
                    padding-left: 100px;
                    display: flex;
                    flex-direction: column;
                }

                .comment {
                    padding-top: 35px;
                    border-bottom: 1px solid #CCCCCC;
                    min-height: 100px;
                    padding-bottom: 20px;
                    margin-right: 70px;
                    font: 14px/19px Arial;
                }

                .data {
                    margin-top: 5px;
                    margin-bottom: 10px;
                }

                .center {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    padding-left: 45px;
                    height: 300px;

                }
                .img {
                    margin-top: 20px;
                    margin-right: 13px;
                    width: 400px;
                }

                .content {
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    height: 280px;
                    padding-left: 90px;
                }

                .body {
                    margin-top: 20px;
                    font: 20px/24px Arial;
                }

                .actions {
                    height: 100%;
                    align-items: center;
                    display: flex;
                    flex-direction: row;
                }

                .footer {
                    align-items: center;
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                }
                   
                   .type {
                        width: 43px;
                        height: 32px;
                        margin-right: 15px;
                        background-color: #255755;
                        border: 1px solid #FFFFFF;
                        border-radius: 2px;
                    }
                   .main {
                        padding-left: var(--main-margin);
                        padding-right: var(--main-margin);
                        background-color: #F4F6F6;
                    }
                   
                   .header {
                        align-items: center;
                        display: flex;
                        flex-direction: row;

                        
                        height: 110px;
                        border-bottom: 2px solid #D3DDDD;                        

                        padding-left: 45px;

                        font: Bold 24px/29px Arial;
                        letter-spacing: 0;
                        color: #000000;

                    }

                    .info {
                        padding-left: 45px;
                        align-items: center;
                        display: flex;
                        flex-direction: row;
                        flex-wrap: wrap;
                        
                        height: 60px;
                        border-bottom: 2px solid #D3DDDD;
                        padding-bottom: 10px;

                        font: Bold 14px/17px Arial;
                        letter-spacing: 0;
                        color: #000000;
                    }

                    .posted, .expert, .citizen, .contributors, .user {
                        display: flex;
                        align-items: center;
                        flex-direction: row;
                        justify-content: flex-start;


                        width: 300px;
                        font: Bold 12px/29px Arial;
                        letter-spacing: 0;
                        color: #707070;

                    }

                    .contributor {
                        display: flex;
                        align-items: center;
                        flex-direction: row;
                        justify-content: flex-start;
                    }
                    
                    .name {
                        font: Bold 14px/17px Arial;
                        letter-spacing: 0;
                        color: #000000;
                    }

                    .date {
                        font: 10px/19px Arial;
                        letter-spacing: 0;
                        color: #707070;
                    }

                    .desc, .name, .date {
                        margin-right: 10px;
                    }

                    .break {
                        flex-basis: 100%;
                        height: 0;
                    }

                `}

                </style>

        </TplGlobal2>

        
    )
}

export default TplPost;